
.map {
  width: 100%;
  height: 332px;
  background-color: #a4c7fc;
  margin-top: -30px;
  margin-bottom: -30px;

  @media (min-width: $tablet-width) {
    height: 581px;
    margin-top: -60px;
    margin-bottom: -60px;
  }

  @media (min-width: $desktop-width) {
    height: 516px;
  }
}

.map__iframe {
  width: 100%;
  height: 100%;
}
